<template>
  <div class="son-channel">
    <h3 class="son-channel-title">{{data.title}}</h3>
    <ul class="video-list" :class="colClass">
      <li v-for="video in data.channelContentList" :key="video.id">
        <router-link  @click.native="clickFn($event,video)" :to=" video.feeRule ===0 ? {path:'/play', query:{id: video.id ,videoSrc: video.videoUrl||video.videoPageUrl} } : ''">
          <div class="video-poster" :style="{backgroundImage: `url(${video.imgUrl})`}">
            <div class="vip-tag" v-if="video.feeRule!==0">会员</div>
            <div class="right-bottom">{{video.rightDownTitle}}</div>
          </div>
        </router-link>
        <div class="movie-name">{{video.title}}</div>
      </li>
    </ul>
    <router-link v-if="showViewMore" :to="{ path:'/more', query:{ cid: data.id, channelType:data.channelType ,pageSize: 100 } }" class="more">
      查看更多
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'son-channel',
  props: {
    data: {
      type: Object,
      required: true
    },
    showViewMore: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      msg: 'this is from son-channel.vue',
    }
  },
  computed: {
    colClass() { //当前显示几列
      return [1, 2, 3].indexOf(this.data.channelType) === -1 ? 'col2' : 'col3'
    }
  },
  methods: {
    clickFn(e, video) {
      if (video.feeRule !== 0) {
        this.$store.commit('showVipDialog')
      }
    }
  }
}
</script>

<style lang='scss' scoped>
.son-channel {
  overflow: hidden;
  .son-channel-title {
    margin-top: 0.12rem;
    margin-bottom: 0.12rem;
    margin-left: 0.08rem;
    border-left: 0.03rem solid #ff3600;
    padding-left: 0.08rem;

    font-size: 0.16rem;
    line-height: 1;
    color: #ff3600;
  }
  .video-list {
    &.col3 {
      .video-poster {
        width: 1.14rem;
        height: 1.52rem;
        .right-bottom {
          color: #ff3600;
        }
      }
      .movie-name {
        width: 1.14rem;
      }
    }
    &.col2 {
      .video-poster {
        width: 1.755rem;
        height: 0.98rem;
        .right-bottom {
          color: #fff;
          font-weight: normal;
        }
      }
      .movie-name {
        width: 1.755rem;
      }
    }
    li {
      margin-left: 0.08rem;
      display: inline-block;
      vertical-align: top;
      .vip-tag,
      .right-bottom {
        position: absolute;
        font-size: 0.12rem;
        text-align: center;
        line-height: 0.2rem;
      }
      .vip-tag {
        top: 0;
        left: 0;
        width: 0.36rem;
        background: #ff3600;
        color: #fff;
      }
      .right-bottom {
        bottom: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.8);
        color: #ff3600;
        padding: 0 0.04rem;
        font-weight: bold;
      }
      .video-poster {
        position: relative;

        display: block;
        width: 100%;
        height: 100%;
        background: no-repeat center/cover;
      }
      .movie-name {
        font-size: 0.12rem;
        margin: 0.05rem 0;
      }
    }
  }
  .more {
    display: block;
    font-size: 0.14rem;
    color: #ff3600;
    line-height: 0.44rem;
    text-align: center;
    border-top: 0.01rem solid #ebebeb;
    border-bottom: 0.1rem solid #ebebeb;
  }
}
</style>



