import _axios from './_axios'
import store from '@/store'
// getChannelList
export default function () {
  return _axios.get('/bannerInfo.service',{ params:{ cid: store.state.currChanelId } })
    .then(res => {
      store.commit('setCurrBannerData',res.data)
    })
}



