import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import play from '@/pages/play'
import more from '@/pages/more'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name: 'index',
      component: index
    },
    {
      path: '/play',
      name: 'play',
      component: play
    },
    {
      path: '/more',
      name: 'more',
      component: more
    }
  ]
})



