<template>
  <div class="channel-list">
    <ul class="channel-list-ul">
      <li v-for="(topChannel,i) in channelList" :class="{active: i===activeIndex }" :key="topChannel.id" @click="switchChannel(topChannel.id,i)">
        {{topChannel.title}}
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import api from '@/api'

export default {
  name: 'channel-list',
  data() {
    return {
      msg: 'this is from channel-list.vue',
      activeIndex: 0
    }
  },
  computed: {
    ...mapState(['channelList'])
  },
  methods: {
    // 切换频道，更新banner main
    switchChannel(chanelId,i) {
      this.activeIndex =i
      this.$store.commit('setCurrChanelId',chanelId)
    }
  },

}
</script>

<style lang='scss' scoped>
@import "~@/assets/css/var.scss";

.channel-list {
  position: absolute;
  top: $header-height;
  height: 0.44rem;
  overflow: auto;
  box-sizing: border-box;
  .channel-list-ul {
    margin-top: ($channel-height - 0.22rem)/2;
    line-height: 0.22rem;
    font-size: 0.16rem;
    & > li {
      margin: 0 0.1rem;
      display: inline-block;
      &.active:after {
        content: "";
        width: 80%;
        margin: 0 auto;
        display: block;
        height: 0.01rem;
        background: #ff3600;
        margin-top: 0.02rem;
      }
    }
  }
}
</style>



