<template>
  <div class="my-main" :class="{'without-channel-list': withoutChannelList}">
    <banner-swiper :key="currChanelId"></banner-swiper>
    <son-channel v-for="sonChannel in currMainData" :data="sonChannel" :key="sonChannel.id"></son-channel>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import api from '@/api'
import bannerSwiper from './main/bannerSwiper'
import sonChannel from './main/sonChannel'
export default {
  name: 'my-main',
  components: {
    bannerSwiper,
    sonChannel
  },
  data() {
    return {
      msg: 'this is from my-main.vue',
    }
  },
  computed: {
    ...mapGetters(['withoutChannelList']),
    ...mapState(['currMainData','currChanelId']),
  },
  watch: {
    currChanelId: {
      handler: function (val, oldVal) {
        api.getMainData()
      },
      immediate: true
    }
  }
}
</script>

<style lang='scss' scoped>
@import "~@/assets/css/var.scss";
.my-main {
  position: absolute;
  top: $header-height + $channel-height;
  bottom: 0;
  width: 100%;
  overflow: auto;
  &.without-channel-list {
    top: $header-height;
  }
}
</style>



