<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <router-link v-for="item in currBannerData" :to="{path:'/play', query:{videoSrc: item.targetUrl, id: item.targetId} }" class="swiper-slide" :key="item.title" :style="{backgroundImage:`url(${item.imgUrl})`}"></router-link>
    </div>
    <div class="pagination"></div>
  </div>
</template>

<script>
import swiper from 'swiper'
import 'swiper/dist/idangerous.swiper.css'
import api from '@/api'
import { mapState } from 'vuex'
export default {
  name: 'banner-swiper',
  data() {
    return {
      msg: 'this is from banner-swiper.vue',
    }
  },
  computed: {
    ...mapState(['currBannerData', 'currChanelId'])
  },
  created() {


  },
  watch: {
    currChanelId: {
      handler: function (val, oldVal) {
        api.getBannerData().then(() => {
          this.$nextTick(() => {
            var mySwiper = new swiper('.swiper-container', {
              autoplay: 5000,//可选选项，自动滑动
              loop: true,//可选选项，开启循环
              pagination: '.pagination'
            })
          })
        })
      },
      immediate: true
    }
  }
}
</script>
<style>
.pagination {
  position: absolute;
  z-index: 20;
  bottom: 0.08rem;
  right: 0.12rem;
  text-align: center;
}
.swiper-pagination-switch {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 8px;
  margin: 0 0.03rem;
  border: 1px solid #fff;
  cursor: pointer;
}
.swiper-active-switch {
  background: #ff3600;
}
</style>

<style lang='scss' scoped>
.swiper-container {
  position: relative;
  margin: 0.08rem auto;
  width: 3.6rem;
  height: 1.94rem;
  background: #ccc;
  overflow: hidden;
  .swiper-slide {
    width: 3.6rem;
    height: 1.94rem;
    background: no-repeat center/cover;
  }
}
</style>



