<template>
  <div class="my-header">
    <div class="logo"></div>
    <a class="download-btn" :href="downloadLink">下载APP</a>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'my-header',
  data() {
    return {
      msg: 'this is from my-header.vue',
    }
  },
  computed:{
    ...mapState(['downloadLink'])
  }
}
</script>

<style lang='scss' scoped>
@import "~@/assets/css/var.scss";
.my-header {
  position: absolute;
  top: 0;
  width: 100%;
  height: $header-height;
  background: #010101;
  overflow: hidden;
  .logo {
    float: left;

    width: 0.71rem;
    height: 0.28rem;
    margin-top: ($header-height - 0.28rem)/2;
    margin-left: 0.12rem;
    background: url(~@/assets/img/logo.png) center/contain;
  }
  .download-btn {
    float: right;
    text-align: center;
    padding: 0 .1rem;
    height: 0.24rem;
    margin-top: ($header-height - 0.24rem)/2;
    margin-right: 0.12rem;
    background: #ff3600;
    border-radius: 0.24rem;
    line-height: 0.24rem;
    font-size: 0.12rem;
    color: #fff;
  }
}
</style>



