<template>
  <div class="more">
    <son-channel v-if="moreData" :data="moreData" :showViewMore="false"></son-channel>
  </div>
</template>

<script>
import api from '@/api'
import sonChannel from '@/components/main/sonChannel'

export default {
  name: 'more',
  components: {
    sonChannel
  },
  data() {
    return {
      msg: 'this is from more.vue',
      moreData: null, // 查看更多页面数据
    }
  },
  created() {
    // console.log(this.$route.query)
    api.getMoreData(this.$route.query).then(
      res => {
        let data = res.data
        data.channelType = this.$route.query.channelType
        if (parseInt(this.$route.query.cid) === 2197) {
          data.title = '最新预告片'
        }
        this.moreData = data
      }
    )
  }
}
</script>

<style lang='scss' scoped>
.more {
}
</style>



