import _axios from './_axios'
import store from '@/store'
// getChannelList
export default function () {
  return _axios.get('/qryVideoChannelContentList.service',{ params:{ cid: store.state.currChanelId } })
    .then(res => {
      store.commit('setCurrMainData',res.data.videoChannelList)
    })
}






