<template>
  <div id="app">
    <router-view/>
    <my-dialog class="vipDialog" v-show="vipDialogShow" message="" btnText="下载app" @click="clickFn" @close="closeFn">
      <div class="download-logo"></div>
      <div class="download-text">该视频为vip视频，请下载app观看</div>
    </my-dialog>
  </div>
</template>

<script>
import api from '@/api'
import myDialog from '@/components/dialog'
api.getChannelList()
import { mapState } from 'vuex'
export default {
  name: 'App',
  components: {
    myDialog
  },
  computed: {
    ...mapState(['vipDialogShow'])
  },
  methods: {
    clickFn() {
      location.href = _downloadLink
      this.$store.commit('closeVipDialog')
    },
    closeFn() {
      this.$store.commit('closeVipDialog')
    }
  },
  mounted(){
    console.log('app mounted')
  }
}
</script>

<style lang="scss">
#app {
  .download-logo {
    width: 0.6rem;
    height: 0.6rem;
    margin: 0.1rem auto 0;
    background: url(~@/assets/img/logo_1.png) no-repeat center/contain;
  }
  .download-text {
    text-align: center;
    padding: .1rem 0;
  }
}
</style>



