<template>
  <div class="play">
    <video v-if="!urlParseRes.query.url" :src="urlParseRes.url" controls="controls" autoplay="autoplay">
      您的浏览器不支持 video 标签。
    </video>
    <div v-else class="iframe-container" :class="urlParseRes.query.url.indexOf('iqiyi')!==-1?'iqiyi':'wasu'">
      <iframe :src="urlParseRes.query.url" frameborder="0" scrolling="no"></iframe>
    </div>
    <template v-if="videoDetail">
      <div class="video-play-detial">
        <i class="view-number-icon"></i>
        <span class="view-number">{{formatViewNumber}}</span>
        <span class="video-source">来源：{{videoDetail.filmSource}}</span>
      </div>
      <div class="video-detial">
        <div  class="video-name">{{videoDetail.title}}</div>
        <div v-if="videoDetail.score" class="video-score">
          <span class="video-score">评分：
            <span class="score-number">{{videoDetail.score}}</span>
          </span>
        </div>
        <div v-if="videoDetail.tags" class="video-tags">类型：{{videoDetail.tags}}</div>
        <div v-if="videoDetail.year" class="video-year">上映年份：{{videoDetail.year}}</div>
        <div v-if="videoDetail.director" class="video-director">导演：{{videoDetail.director}}</div>
        <div v-if="videoDetail.leadingRole" class="video-leadingRole">主演：{{videoDetail.leadingRole}}</div>
        <div v-if="videoDetail.description" class="video-desc">简介：{{videoDetail.description}}</div>
      </div>
    </template>
  </div>
</template>

<script>
import queryString from 'query-string'
import api from '@/api'
export default {
  name: 'play',
  data() {
    return {
      msg: 'this is from play.yvue',
      videoDetail: null
    }
  },
  computed: {
    urlParseRes() {
      let url = this.$route.query.videoSrc
      return queryString.parseUrl(url)
    },
    formatViewNumber() {
      if (!this.videoDetail) return
      let vNum = this.videoDetail.viewNumber
      if (vNum < 10000) {
        return vNum
      } else if (vNum < 100000000) {
        return (vNum / 10000).toFixed(2) + '万'
      } else {
        return (vNum / 100000000).toFixed(2) + '亿'
      }
    }
  },
  beforeCreate() {
    api.getVideoDetail({ id: this.$route.query.id }).then(res => {
      // console.log(res.data)
      this.videoDetail = res.data
    })
  }
}
</script>

<style lang='scss' scoped>
.play {
  .iframe-container {
    height: 2.1rem;
    width: 100%;
    overflow: hidden;
    background: #000;
  &.iqiyi{
    iframe{
      margin-top: -0.9rem;
    }
  }
   &.wasu{
    iframe{
      margin-top: -0.76rem;
    }
  }
    iframe {
      width: 100%;
      height: 6rem;
    }
  }
  video {
    width: 100%;
    background: #000;
    height: 2.1rem;
  }
  .video-play-detial {
    height: 0.42rem;
    font-size: 0.12rem;
    padding-top: (0.42rem - 0.16rem) / 2;
    box-sizing: border-box;
    & > * {
      vertical-align: middle;
      float: left;
    }
    .view-number-icon {
      width: 0.16rem;
      height: 0.16rem;
      margin-left: 0.08rem;
      background: url(~@/assets/img/play.png) no-repeat center/cover;
    }
    .view-number {
      margin-left: 0.03rem;
    }
    .video-source {
      margin-left: 0.22rem;
    }
  }
  .video-detial {
    height: 100px;
    border-top: 0.2rem solid #ebebeb;
    font-size: 0.12rem;
    line-height: 0.22rem;
    padding: 0 0.08rem;
    .score-number {
      color: #ff3600;
      font-weight: bold;
    }
    .video-name {
      font-size: 0.18rem;
      margin-top: 0.16rem;
      margin-bottom: 0.22rem;
    }
  }
}
</style>



