import Vue from 'vue'
import Vuex from 'vuex'
import Promise from 'promise-polyfill'
// To add to window
if (!window.Promise) {
    window.Promise = Promise
}
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    downloadLink: _downloadLink, // _downloadLink 定义在了index.html中，方便日后更新
    msg: 'msg from vuex',
    isMobile: /(iPhone|iPod|iPad|Android|ios)/.test(navigator.userAgent),
    isProd: process.env.NODE_ENV === 'production',
    playUrl: '', // 进行本地存储，否则直接进入播放页无法播放
    channelList: null, // 频道列表（nav）
    currChanelId: NaN, // 当前频道id
    currMainData: null, // 当前频道对应的main组件数据
    currBannerData: null,
    vipDialogShow: false
  },
  getters: {
    withoutChannelList(state) {
      return state.channelList && state.channelList.length === 1
    }
  },
  mutations: {
    setPlayUrl(state, playload) {
      state.playUrl = playload
    },
    setChannelList(state, playload) {
      state.channelList = playload
    },
    setCurrChanelId(state, playload) {
      state.currChanelId = playload
    },
    setCurrMainData(state, playload) {
      state.currMainData = playload
    },
    setMoreData(state, playload) {
      state.currMoreData = playload
    },
    setCurrBannerData(state, playload) {
      state.currBannerData = playload
    },
    showVipDialog(state){
      state.vipDialogShow =true
    },
    closeVipDialog(state){
      state.vipDialogShow =false
    }
  }
})

export default store



