<template>
  <div class="index" v-if="channelList">
    <my-header></my-header>
    <channel-list v-if="!withoutChannelList"></channel-list>
    <my-main></my-main>
  </div>
</template>

<script>
import channelList from '@/components/channelList'
import myHeader from '@/components/header'
import myMain from '@/components/main'
import { mapGetters,mapState} from 'vuex'

export default {
  name: 'index',
  components: {
    myHeader,
    channelList,
    myMain
  },
  data() {
    return {
      msg: 'this is from index.vue',
    }
  },
    computed: {
    ...mapGetters(['withoutChannelList']),
    ...mapState(['channelList'])
  }
}
</script>

<style lang='scss' scoped>
.index {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
</style>



