<template>
  <div class="my-dialog">
    <div class="content">
      <div class="title">{{title}}</div>
      <div class="message">
        <slot/>
      </div>
      <div class="confirm-btn" @click="$emit('click')">{{btnText}}</div>
      <div class="close" @click="$emit('close')">×</div>
    </div>
    <div class="cover" v-if="showCover">

    </div>
  </div>
</template>

<script>
export default {
  name: 'my-dialog',
  props: {
    title: {
      type: String,
      default: ''
    },
    btnText: {
      type: String,
      default: '确定'
    },
    showCover: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      msg: 'this is from my-dialog.vue',
    }
  }
}
</script>

<style lang='scss' scoped>
.my-dialog {
  & > .content {
    position: fixed;
    left: 0;
    top: 0;
    width: 2.8rem;
    z-index: 10000;
    background: rgba(255,255,255,.95);
    border-radius: 0.06rem;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: .1rem 0.2rem;
    box-sizing: border-box;
    .title {
      text-align: center;
      font-size: 0.2rem;
      line-height: 2.4;
    }
    .message {
      margin-bottom: 0.1rem;
      font-size: 0.15rem;
      min-height: 0.5rem;
      color: #555;
    }
    .confirm-btn {
      background: #ff3600;
      color: #fff;
      border-radius: .24rem;
      text-align: center;
      font-size: 0.14rem;
      line-height: .3rem;
      margin: 0 auto ;
      width: 1.6rem;
    }
    .close {
      position: absolute;
      top: 0;
      right: 0;
      width: 0.3rem;
      height: 0.3rem;
      text-align: center;
      font-size: 0.2rem;
      color: #ccc;
    }
  }
  & > .cover {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 9999;
    background: rgba(0, 0, 0, 0.4);
  }
}
</style>



